.catalogConteiner {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  max-width: 1184px;
  align-items: flex-start;
  gap: 29px;
  flex-shrink: 0;
  flex-wrap: wrap;
}
.cards {
  width: 274px;
  height: 426px;
}
.carImg {
  width: 274px;
  height: 268px;
  object-fit: cover;
  border-radius: 14px;
  background: linear-gradient(
      180deg,
      rgba(18, 20, 23, 0.5) 2.5%,
      rgba(18, 20, 23, 0) 41.07%
    ),
    #f3f3f2;
  margin-bottom: 14px;
}

.carTitle {
  display: flex;
  justify-content: space-evenly;
  align-items: baseline;
}

.carTitleMake {
  margin: 0;
  margin-bottom: 8px;
  margin-right: auto;
  color: #121417;
  font-family: 'Manrope', sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.carTitleModel {
  color: #3470ff;
  font-family: 'Manrope', sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.cardInfo {
  margin: 0;
  width: 270px;
  max-height: 40px;
  color: rgba(18, 20, 23, 0.5);
  font-family: 'Manrope', sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  overflow: hidden;
  margin-bottom: 28px;
}

.cardBtn {
  display: flex;
  width: 274px;
  height: 44px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 12px;
  border: none;
  background: #3470ff;
  color: white;
  font-family: 'Manrope', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  transition: background 0.5s ease;
}

.cardBtn:hover {
  background: #0b44cd;
  cursor: pointer;
}

.carImgBtn {
  position: relative;
}
.carFavoriteBtn {
  position: absolute;
  border: none;
  top: 14px;
  right: 14px;
  background: transparent;
  cursor: pointer;
}
